import React from 'react';
import "./Works.css"
import Upwork from "../../img/Upwork.png"
import fiverr from "../../img/fiverr.png"
import Amazon from "../../img/amazon.png"
import Facebook from "../../img/Facebook.png"
import Shopify from "../../img/Shopify.png"
import { useContext } from 'react';
import { themeContext } from '../../context/context';
import { motion } from 'framer-motion';
import { Link } from 'react-scroll';

const Works = () => {
    const theme= useContext(themeContext)
    const transition={duration :3.5,type:"spring"}
    const darkmode=theme.state.darkmode
    return (
        <div className='works'>
             <div className="awesome w-awesome">
                <span style={{color:darkmode? "white" :""}}>Cloned websites </span>
                <span>of these brands</span>
                <span>These are the websites which i have cloned
                    <br />
                      by understanding there features 
                     <br />
                      the problem they are solving.
                     <br />
                     also implimented UI/UX 
                </span>
                
                <Link className='Link' spy={true} to="Contact" smooth={true}>
                <button className='button s-button'>Hire me</button>
                       </Link>
              
               
                <div className='s-blur'></div>
            </div>
            {/* right */}
            <div className="w-right">
                <motion.div
                   initial={{rotate:45}}
                   whileInView={{rotate:0}}
                   viewport={{margin:"-40px"}}
                   transition={transition}
                className="w-maincircle">
                    <div className="seccircle">
                        <img src={Upwork} alt="" />
                    </div>
                    <div className="seccircle">
                        <img src={fiverr} alt="" />
                    </div>
                    <div className="seccircle">
                        <img src={Facebook} alt="" />
                    </div>
                    <div className="seccircle">
                        <img src={Shopify} alt="" />
                    </div>
                    <div className="seccircle">
                        <img src={Amazon} alt="" />
                    </div>
                </motion.div>
                <div className="w-backcircle bluecircle"></div>
                <div className="w-backcircle yellowcircle"></div>
            </div>
        </div>
    );
};

export default Works;